s de encargarse de presentar la narrativa. Por esta razón, deben ser profesionales capacitados. De todos sus posibles roles o capacidades, tres son los más importantes: la capacidad de intermediación, la de ofrecer seguridad y la de transmitir la narrativa de la experiencia."),_c('br'),_c('br'),_vm._v("Por otra parte, para que un turista pueda vivir plenamente la experiencia turística, es preciso que valide al guía como tal, estableciéndose entre ambos un vínculo de confianza. Para que ello ocurra, este debe proporcionar información, seguridad y confort al visitante, evitándole posibles problemas o riesgos. Por último, el guía debe ser capaz de transmitir al turista —de manera atractiva y envolvente— la narrativa de la experiencia, mediante el uso de habilidades comunicativas que mantengan el interés del visitante."),_c('br'),_c('br'),_vm._v("Estas capacidades, más otras como la empatía y el profesionalismo, resultan deseables en todos los mediadores, ya que son fundamentales para ofrecer una experiencia coherente y de calidad. Por esta razón, una empresa turística que quiera desarrollar una buena experiencia no puede descuidar a sus mediadores; todo lo contrario: debe procurar involucrarlos activamente en el cumplimiento de los objetivos de la empresa y de la experiencia, lo que les permitirá transmitir la narrativa de mejor manera.")]),_c('i',{staticClass:"fas fa-quote-right"})]),_c('div',{staticClass:"bloque-texto-d__autor"},[_c('p',{staticClass:"mb-0 d-flex justify-content-end"},[_c('b',[_vm._v("- Nota. Tomado de SERNATUR (2017)")])])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row align-items-center mb-5"},[_c('div',{staticClass:"col-lg-6 mb-4 mb-lg-0",attrs:{"data-aos":"fade-left"}},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/img14.svg")}})])]),_c('div',{staticClass:"col-lg-6"},[_c('p',[_vm._v("Hay que destacar la importancia que tiene el guía de turismo en los destinos, compromete a estos prestadores de servicios a estar altamente capacitados y en constante formación, la cual no termina una vez obtenida su tarjeta profesional, su formación debe darse a lo largo del ejercicio de su actividad y acorde con su vocación y campo de acción, demostrado está que la responsabilidad que tiene frente a los turistas y el mismo destino turístico es inmensa y así mismo debe ser su compromiso.")]),_c('p',[_vm._v("También se debe tener en cuenta que hay otros actores que forman parte de la cadena de la prestación de servicio y de la experiencia diseñada, todos, de igual manera, deben estar capacitados para poder brindar el mejor servicio y estar alineados e identificados con la experiencia que se está ofreciendo.")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-4"},[_c('div',{staticClass:"col-lg-4 mb-4 mb-lg-0",attrs:{"data-aos":"fade-right"}},[_c('figure',[_c('img',{staticClass:"m-auto",staticStyle:{"width":"450px"},attrs:{"src":require("@/assets/curso/tema1/img19.png")}})])]),_c('div',{staticClass:"col-lg-8"},[_c('p',{staticClass:"mb-4"},[_c('b',{staticClass:"txt-color1"},[_vm._v("Seguridad")])]),_c('p',[_vm._v("Como se interpreta, la experiencia turística involucra al turista, lo hace partícipe de las actividades; en este sentido, el turismo de aventura, en escenarios naturales y a veces agrestes, motivará a los turistas a participar de manera activa en lo que se le proponga. Es importante entonces identificar el grado de riesgo y dificultad que tenga cada una de las actividades propuestas en una experiencia, con el fin de minimizar los riesgos a través de elementos como planes de contingencia, inclusión y uso de equipos certificados, y la implementación de normas técnicas sectoriales de acuerdo con las actividades a desarrollar; no se puede olvidar que cualquier incidente o accidente que se presente en un turista puede ser responsabilidad de quien diseña la experiencia